/* Swiper 컨테이너 */
.swiper {
  width: 150px;
  /* 카드 너비 */
  height: 200px;
  /* 카드 높이 */
}

/* Swiper 슬라이드 (카드) */
.swiper-slide {
  background-color: #3d79cd;
  /* 카드 배경색 (흰색) */
  border-radius: 10px;
  /* 카드 모서리 둥글게 */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  /* 카드 그림자 */
  color: white;
  /* 배경색 변경 */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* 활성 슬라이드 (카드) */
.swiper-slide-active {
  transform: scale(1.1);
  /* 활성 카드 확대 */
}

.slide-content {
  // 슬라이드 내용 클래스명
  width: 100%;
  box-sizing: border-box;
  padding: 10px;

  .slide-title {
    // 슬라이드 타이틀 클래스명
    font-weight: bold;
    margin-bottom: 5px;
    text-align: center;
    /* 텍스트 중앙 정렬 추가 */
    font-size: 16px;
  }

  .slide-desc {
    // item.desc 클래스명 추가
    width: 100%; // 컨테이너 너비 설정 (필요한 경우)
    word-break: break-all;
    white-space: normal !important;
    word-wrap: break-word; // 브라우저 호환성을 위한 추가 속성 (선택 사항)
    overflow: hidden; // 텍스트 넘침 방지 (선택 사항)
    text-overflow: ellipsis; // 텍스트 넘칠 경우 말줄임표 (선택 사항)
  }

}


  /* 타이틀 컨테이너 스타일 */
  .title-container {
    display: flex;
    /* Flexbox 레이아웃 사용 */
    justify-content: center;
    /* 수평 중앙 정렬 */
    align-items: center;
    /* 수직 중앙 정렬 */
  }

  /* 타이틀 텍스트 스타일 */
  .title-text {
    font-size: 15px;
    /* 폰트 크기 설정 */
    font-weight: bold;
    /* 폰트 굵기 설정 */
    color: #333;
    /* 폰트 색상 설정 */
    margin-bottom: 10px;
    word-wrap: break-word; // 브라우저 호환성을 위한 추가 속성 (선택 사항)
    overflow: hidden; // 텍스트 넘침 방지 (선택 사항)
    text-overflow: ellipsis; // 텍스트 넘칠 경우 말줄임표 (선택 사항)
    /* 텍스트 그림자 추가 (선택 사항) */
  }

  /* 애니메이션 효과 (페이드 인) */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  .title-text {
    /* 애니메이션 적용 */
    animation: fadeIn 1s ease-in-out;
    /* 1초 동안 서서히 나타나는 효과 */
  }

  /* 선택된 아이템 컨테이너 스타일 */
.selected-item-container {
  display: flex;          /* Flexbox 레이아웃 사용 */
  justify-content: center; /* 수평 중앙 정렬 */
  align-items: center;     /* 수직 중앙 정렬 */
  margin: 10px;       /* 위쪽 여백 추가 (선택 사항) */
}

/* 선택된 아이템 텍스트 스타일 */
.selected-item-text {
  font-size: 17px;       /* 타이틀보다 작은 폰트 크기 */
  font-weight: bold;     /* 폰트 굵기 설정 */
  color: #eb7222;           /* 폰트 색상 설정 */
}

.selected-item-text { /* 애니메이션 적용 */
  animation: fadeIn 1s ease-in-out; 
}